<template>
  <div class="category-wrapper">
    <div class="dialog">
      <div class="header">
        <div class="icon-wrapper">
          <i class="back" @click="back"></i>
        </div>
        <div class="text">
          全部分类
        </div>
        <div class="icon-wrapper">
          <i class="icon-category"></i>
        </div>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <ul class="list">
            <li class="parent" v-for="(item,index) in list">
              <div class="title" @click="toggle">
                <span class="text">{{item.key}}</span>
                <i></i>
              </div>
              <ul class="detail hide" v-if="item.list && item.list.length">
                <li class="item" ref="item" v-for="child in item.list" @click.stop="select">
                  {{child}}
                </li>
              </ul>
            </li>
          </ul>
        </scroll>
      </div>
      <div class="footer">
        <span class="cancel" @click="back">取消</span>
        <span class="confirm" @click="confirm">确定</span>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import {mapActions} from 'vuex'
  import {hasClass,addClass,removeClass} from 'common/js/dom'

  export default {
    data () {
      return {
        query: '',
        list: []
      }
    },
    created () {
      this._getCategoryList()
    },
    methods: {
      back() {
        this.$emit('hide')
      },
      confirm() {
        this.$emit('query', this.query)
        this.back()
      },
      toggle(e) {
        let el = e.srcElement || e.target
        let currentNode = hasClass(el,'title') ? el : el.parentNode
        if(hasClass(currentNode, 'active')){
          removeClass(currentNode, 'active')
        } else {
          addClass(currentNode, 'active')
        }
        let nextElement = currentNode.nextElementSibling
        if(hasClass(nextElement, 'hide')){
          removeClass(nextElement, 'hide')
        } else {
          addClass(nextElement, 'hide')
        }
      },
      select(e) {
        let el = e.srcElement || e.target
        let items = this.$refs.item
        for(let i = 0; i < items.length; i++){
          let item = items[i]
          if(hasClass(item, 'active')){
            removeClass(item, 'active')
          }
        }
        addClass(el, 'active')
        this.query = el.innerText
        let parent = el.parentNode
        removeClass(parent, 'hide')
      },
      _getCategoryList() {
        this.list = ([
          {
            key: '酒水饮料',
            list: [
              '啤酒',
              '红酒',
              '白酒'
            ]
          },
          {
            key: '食品',
            list: [
              '饺子',
              '米饭',
              '馒头',
              '包子',
              '面条'
            ]
          },{
            key: '电子产品',
            list: [
              '小米',
              '华为',
              '苹果'
            ]
          },{
            key: '水果',
            list: [
              '苹果',
              '西瓜',
              '橘子',
              '哈密瓜'
            ]
          }
        ])
      }
    },
    components: {
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active,.slide-leave-active
    transition all 0.3s
  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .category-wrapper
    background rgba(0,0,0,0.4)
    position fixed
    left 0
    right 0
    top 0
    bottom 0
    z-index 2
    .dialog
      position absolute
      left 40px
      right 0
      top 0
      bottom 0
      .header
        position fixed
        height 44px
        top 0
        left 40px
        right 0
        box-sizing border-box
        border-bottom 1px solid #E6E6E6
        background $color-header-background
        display flex
        justify-content center
        align-items center
        .icon-wrapper
          width 50px
          height 100%
          .back
            position absolute
            background url("~common/image/back.png") no-repeat
            background-size contain
            width 8px
            height 15px
            left 15px
            top 15px
            &:before
              position absolute
              left -10px
              right -10px
              top -10px
              bottom -10px
              content ''
        .text
          display flex
          flex 1
          height 100%
          align-items center
          justify-content center
          font-weight bold
          color #333
          font-size $font-size-medium-x
        .icon-wrapper
          .icon-category
            position absolute
            background url("~common/image/purchase/category.png") no-repeat
            background-size contain
            width 14px
            height 13px
            right 15px
            top 16px
            &:before
              position absolute
              left -10px
              right -10px
              top -10px
              bottom -10px
              content ''
      .content-wrapper
        position absolute
        left 0
        right 0
        top 44px
        bottom 44px
        background #fff
        display flex
        .content
          width 100%
          overflow hidden
          .list
            width 100%
            .parent
              border-bottom 1px solid #f2f2f2
              .title
                display flex
                align-items center
                justify-content space-between
                height 42px
                box-sizing border-box
                padding-left 15px
                padding-right 15px
                .text
                  color #3d3d3d
                  background #fff
                  font-size $font-size-medium-m
                i
                  display flex
                  position relative
                  background url("~common/image/down.png") no-repeat
                  background-size contain
                  width 10px
                  height 6px
                  &:before
                    position absolute
                    left -10px
                    top -10px
                    right -10px
                    bottom -10px
                    content ''
                &.active i
                  background url("~common/image/up.png") no-repeat
                  background-size contain
              .detail
                background #F5F5F5
                &.hide
                  display none
                .item
                  color #3d3d3d
                  font-size $font-size-medium
                  box-sizing border-box
                  padding-left 30px
                  display flex
                  align-items center
                  height 42px
                  &.active
                    color #EB4747
      .footer
        position fixed
        height 44px
        bottom 0
        left 40px
        right 0
        display flex
        span
          display flex
          flex 1
          height 100%
          align-items center
          justify-content center
          font-size $font-size-medium-m
          &.cancel
            color #999999
            background #e6e6e6
          &.confirm
            color #fff
            background #eb4747

</style>



