<template>
  <div class="guess-wrapper">
      <div class="tilte">{{title}}</div>
      <ul class="word-wrapper">
        <li class="item" v-for="(keyword,index) in list" @click="selectItem(keyword)">
            {{keyword}}
        </li>
      </ul>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data () {
      return {

      }
    },
    props: {
        list: {
            type: Array,
            default: []
        },
        title: {
            type: String,
            default: '猜你喜欢'
        }
    },
    methods: {
      selectItem(keyword) {
        this.$emit('query', keyword)
      }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/variable"

    .guess-wrapper
      width 100%
      height 100%
      color $color-background
      .tilte
        display flex
        height 40px
        align-items center
        margin-left 10px
        color #999
        font-size $font-size-small-x
      .word-wrapper
        background #fff
        display flex
        flex-wrap wrap
        box-sizing border-box
        padding-left 15px
        padding-right 15px
        justify-content space-between
        .item
          margin-top 20px
          margin-right 10px
          background #f0f0f0
          color #4c4c4c
          font-size $font-size-medium
          min-width 60px
          height 30px
          display flex
          justify-content center
          align-items center
          &:last-child
            margin-bottom 20px

</style>



